import axiosClient from "./axiosClient";

const productApi = {
  getProducts: () => {
    const url = "/products/";
    return axiosClient.get(url);
  },
  getProductDetail: () => {
    const url = "/productDetail/";
    return axiosClient.get(url);
  },
  getKeywords: () => {
    const url = "/keywords/";
    return axiosClient.get(url);
  },
  getImageCategory: () => {
    const url = "/imageCategory/";
    return axiosClient.get(url);
  },
  getProductDetailById: (id) => {
    const url = `/productDetail/${id}`;
    return axiosClient.get(url);
  },
};
export default productApi;
