import {
  GET_PRODUCTS_SUCCESS,
  GET_PRODUCT_DETAIL_SUCCESS,
  GET_IMAGE_CATEGORY_SUCCESS,
  GET_KEYWORDS_SUCCESS,
} from "./actionTypes";

export function loadProductsSuccess(products) {
  return { type: GET_PRODUCTS_SUCCESS, products };
}
export function loadProductDetailSuccess(products) {
  return { type: GET_PRODUCT_DETAIL_SUCCESS, products };
}
export function loadImageCategorySuccess(imgCategory) {
  return { type: GET_IMAGE_CATEGORY_SUCCESS, imgCategory };
}
export function loadKeywordsSuccess(keywords) {
  return { type: GET_KEYWORDS_SUCCESS, keywords };
}

