import initState from "./initStates";
import {
  GET_IMAGE_CATEGORY_SUCCESS,
  GET_KEYWORDS_SUCCESS,
} from "../actions/actionTypes";

export default function productDetailReducer(state = initState.orther, action) {
  switch (action.type) {
    case GET_IMAGE_CATEGORY_SUCCESS:
      return {...state.orther, keywords: state.keywords, imgCategory: action.imgCategory};
    case GET_KEYWORDS_SUCCESS:
      return { ...state.orther, keywords: action.keywords, imgCategory: state.imgCategory };
    default:
      return state;
  }
}
