import React, { Component } from "react";
import { withRouter, Route, Switch } from "react-router-dom";
import ManageHomePage from "./ManageHomePage";
import ManageProductsPage from "./ManageProductsPage";
import ManageProductPage from "./ManageProductPage";
import Home from "./Home";


class App extends Component {
  render() {
    return (
      <Switch>
        <Route exact path='/' component={ManageHomePage}/>
        <Route path='/products/:slugg' component={ManageProductsPage}/>
        <Route path='/products' component={ManageProductsPage}/>
        <Route path='/product/:slugg' component={ManageProductPage}/>
        <Route path='/product' component={ManageProductPage}/>
        <Route path='/test' component ={Home}/>
      </Switch>
    );
  }
}
export default withRouter(App);
